/**
 * webApp入口
 * 路由相关
 */
import 'babel-polyfill';
import React from 'react';
import ReactDOM from 'react-dom';
import {
  // HashRouter as Router,
  BrowserRouter as Router,
  Route,
  Redirect,
  Switch
} from 'react-router-dom';
import GetToken from './data/GetToken';
import ShareReg from './screens/ShareReg';
import Pay from './screens/Pay';

import './styles/index.css';

// import Test from './Test';
import registerServiceWorker from './registerServiceWorker';

// 当设置为 true 时，在导航的过程中整个页面将会刷新。 只有当浏览器不支持 HTML5 的 history API 时，才设置为 true。
let supportsHistory = 'pushState' in window.history;
supportsHistory = !supportsHistory;
let u = navigator.userAgent;
let isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/); // ios终端
let isWeChat = u.indexOf('MicroMessenger') > -1;  // 是否微信
if (isiOS && isWeChat) {
  // ios微信需要刷新以确保路由地址跟新
  supportsHistory = true;
}

ReactDOM.render(
  <Router forceRefresh={supportsHistory}>
    <div className='rootson'>
      <Switch>
        <Route exact path="/invite2/:phone?" component={Pay} />
        <Route path="/invite/:phone" component={ShareReg} />
      </Switch>
    </div>
  </Router>, document.getElementById('root'));
registerServiceWorker();

//route中的/是初始加载的页面,
//TabBar是底部导航栏，因为tabBar里面会导航到Search页面，所以要放进Switch里面
//组件也是标签组成的。只要Route和标签（组件）处于一个标签里面，组件就可以使用Route定义的页面。
//没处于Switch里面的路由，不会处于互斥状态。比如，如果匹配/Ho，在/Ho后面还有个/Hom，也会被匹配到，这样就会渲染到一个页面。


