/**
 * fetch
 */

import axios from 'axios';
import storage from './storage';
const APPLICATION_JSON = 'application/json; charset=UTF-8';

// const baseHost = 'http://pytaoapitest.tuidanke.com/api/';
// const baseHost = '/pytao/';
// const baseHost = '/api/';

let defaultOptions = {
    host: '',
    method: 'get',
    mode: 'cors',
    form: true,
    credentials: 'include',
    beforeSend: () => { return Promise.resolve() },//返回一个.then()函数。另外，resolve('any code ')使得then()启用
    timeout: 5000
}

/**
 * @param {any} options
 * - {String} method http请求类型 'post','get'等，默认为'get'
 * - {Object} params url参数
 * - {Object} data 数据
 * - {Number} timeout 过期时间(毫秒)，默认为3000ms
 * - {Boolean} form 是否是表单类型，默认为是，content-type将会设置成form-data，如果为否，content-type会设置成application/json
 * @returns
 */
function http(options) {
    // 合并默认配置
    options = {
            ...defaultOptions,
            ...options
        }
        // 请求是否中断标志
    let isAborted = false,
        httpTimeout = null;
    let promise = new Promise((resolve, reject) => {
        let { host, url, timeout, form, headers = {}, data, params = {}, ...fetchOptions } = options;
        url = host + url;

        // 设置headers
        let requestHeaders = new Headers();
        for (let key in headers) {
            if (typeof headers[key] === 'function') {
                requestHeaders.append(key, headers[key]());
            } else {
                requestHeaders.append(key, headers[key]);
            }
        }
        //console.log(requestHeaders)
        //requestHeaders.append('Accept', 'text/html')

        fetchOptions.headers = requestHeaders;
        fetchOptions.headers = {//直接设置header!
            ...requestHeaders,
            token: storage.localItem('access_token')
        };

        let paramStr = [];
        for (let key in params) {
            paramStr.push(`${key}=${encodeURIComponent(params[key])}`);
        }

        if (paramStr.length) {
            url += '?' + paramStr.join('&');
        }

        // 设置了data
        if (data) {
            if (form === true) {
                // 设置formData
                let requestFormData = new FormData();
                for (let key in data) {
                    requestFormData.append(key, data[key]);
                }
                if (axios) {
                    requestHeaders.append('Content-Type', APPLICATION_JSON);
                    fetchOptions.data = requestFormData;
                } else {
                    fetchOptions.body = requestFormData;
                }
            } else {
                requestHeaders.append('Content-Type', APPLICATION_JSON);
                fetchOptions.body = JSON.stringify(data);
            }
        }

        // 过期
        if (timeout) {
            httpTimeout = setTimeout(() => {
                // 过期了也会中断
                isAborted = true;
                reject({ code: -2, message: '请求超时' });
            }, timeout);
        }
        options.beforeSend(fetchOptions).then(() => {
            // console.log(JSON.stringify(fetchOptions));
            console.log(url);
            // if (axios) {
            fetchOptions.url = url;
            axios(fetchOptions)
                .then(res => {
                    // console.log('请求成功：', res);
                    if (res.status === 200) {
                        console.log('请求成功：', res.data);
                        resolve(res.data);
                    } else {
                        reject({ code: res.status || -1, message: '请求失败，请检查网络链接状态以及应用权限' });
                    }
                }).catch(e => {
                    console.log('请求失败：', e);
                    reject(e);
                })
                // } else {
                //   fetch(url, fetchOptions)
                //     .then(res => {
                //       // console.log(res);

            //       if (isAborted) return
            //       clearTimeout(httpTimeout);
            //       // 非200-299
            //       if (!res.ok) {
            //         return reject({ code: res.status || -1, message: '请求失败，请检查网络链接状态以及应用权限' });
            //       }

            //       // 处理dateType
            //       if (res[dataType] && dataType !== 'raw') {
            //         res[dataType]().then((data) => {
            //           // console.log(data);
            //           if (data.code <= 200) {
            //             resolve(data)
            //           } else {
            //             // if (data.code === 401) {
            //             //   // 登录过期
            //             // }
            //             reject({ code: data.code, message: data.msg })
            //           }
            //         })
            //       } else {
            //         resolve(res)
            //       }
            //     }, err => {
            //       if (isAborted) return
            //       clearTimeout(httpTimeout);
            //       reject({ code: err.status || -1, message: '请求失败，请检查网络链接状态以及应用权限' })
            //     })
            // }
        })
    })

    promise.abort = () => {
        isAborted = true;
        clearTimeout(httpTimeout);
    }

    return promise
}

/**
 * 配置默认设置
 * @param {*} options
 * -
 */
http.setDefaultOptions = (options) => {
    defaultOptions = {
        ...defaultOptions,
        ...options
    }
}

/**
 * get请求
 * @param {*} options
 * @return {Promise} fetch的promise
 */
http.get = (options) => {
    return http(options)
}

/**
 * post请求
 * @param {*} options
 * @return {Promise} fetch的promise
 */
http.post = (options) => {
    return http({
        ...{
            method: 'post',
        },
        ...options
    })
}

/**
 * delete请求
 * @param {*} options
 * @return {Promise} fetch的promise
 */
http.delete = (options) => {
    return http({
        ...{
            method: 'delete',
        },
        ...options
    })
}

/**
 * put请求
 * @param {*} options
 * @return {Promise} fetch的promise
 */
http.put = (options) => {
    return http({
        ...{
            method: 'put',
        },
        ...options
    })
}

export default http


