/**
 * 判断cookie里是否有登录标识token
 * 有token则不请求
 * 无token则请求，并存入cookie
 */
import storage from './storage';
import request from './request';
import axios from 'axios';

function GetToken() {
  let loginCode = window.location.search;

  // 这里是测试专用，给路由添加用户标识
  // if (!loginCode) {
  //   window.location.search = '?u=1c50147';
  //   // window.location.search = '?u=VsfRfF';
  // }

  // 去除无关的window.location.search参数，只保留用户标识
  let str = loginCode.match(/&(\S*)/);
  let reg;
  if (str && str[1]) {
    reg = '&' + str[1];
    loginCode = loginCode.replace(reg, '');
  }
  let code = loginCode.replace('?u=', '');

  if (window.history.length == 1) {
    storage.delCookie('token');
    storage.localItem('uCode', code);
    request.setDefaultOptions({
      beforeSend: (options) => {
        return axios('/pytao/share/' + `${code}` + '/1', {
          headers: {'content-type': 'application/json; charset=UTF-8'},
          credentials: 'include'
        }).then(res => {
          const { data } = res.data;
          // console.log(data);
          storage.setCookie('token', data, 'd15');
        }).catch(e => {
          console.log(e)
        });
      }
    });
  }

  // 判断用户标识是否改变
  if (!storage.localItem('uCode')) {
    storage.localItem('uCode', code);
  } else {
    if (storage.localItem('uCode') !== code) {
      storage.delCookie('token');
      storage.localItem('uCode', code);

      request.setDefaultOptions({
        beforeSend: (options) => {
          return axios('/pytao/share/' + `${code}` + '/1', {
            headers: {'content-type': 'application/json; charset=UTF-8'},
            credentials: 'include'
          }).then(res => {
            const { data } = res.data;
            // console.log(data);
            storage.setCookie('token', data, 'd15');
          }).catch(e => {
            console.log(e)
          });
        }
      });
    }
  }


  if (!storage.getCookie('token')) {
    // 请求token，并存入cookie
    request.setDefaultOptions({
      beforeSend: (options) => {
        if (!storage.getCookie('token')) {
          return axios('/pytao/share/' + `${code}` + '/1', {
            headers: {'content-type': 'application/json; charset=UTF-8'},
            credentials: 'include'
          }).then(res => {
            const { data } = res.data;
            // console.log(data);
            storage.setCookie('token', data, 'd15');
          }).catch(e => {
            console.log(e)
          });
        } else {
          // cookie已存在则返回默认
          return Promise.resolve();
        }
      }
    });
  }
}

export default GetToken;



