  //   let cookieValue = []
  //   let cookieKey = []
  //   cookie.map(item => {
  //     let [ key , value ]  = item.split("=")
  //     value ? cookieValue.push(value):null;
  //     value ? cookieKey.push(key):null;
  //   })
  //   return [[...new Set(cookieKey)], [...new Set(cookieValue)] ];
  // },

  // clearCookie() { //清除cookie
  //   this.getCookie()[0].map(item => {
