/**
 * 注册
 */
import React, { Component } from 'react';
import '../styles/ShareReg.css';
import Toast from '../components/Toast';
import Loading from '../components/Loading';
// import Share from '../screens/Share';
import Clipboard from 'clipboard';
import request from '../data/request';
import Tao from '../images/ic_launcher.png';
import tri from '../images/tri.png';
import dtri from '../images/dtri.png';

class Reg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      master_phone: 0,
      phone: '',
      types: -1,
      show: false,
      code: '',
      loading: false,
      timeout: 60,
      click: false,
      tri: false
    };
  }

  copy = () => {  
    this.clipboard.on('success', (e) => {
        console.log('成功复制')
      this.setState({
        clipboardFail: 'success'
      });
    });
    this.clipboard.on('error', (e) => {
        this.setState({
            clipboardFail: 'fail'
        });
    });
  }

  regParams = {}//注册的参数

  reg = () => {
    this.regParams = {
      phone: this.state.phone,
      master_phone: this.state.master_phone,
      types: this.state.types
    }
    const { phone } = this.state
    if(!(this.state.phone)){
      this.toast.show("手机号不能为空！")
      return
    }
    this.setState({
      loading: true
    })
    this.regRequest = request.post({
      url: '/api/invite/acceptInvite',
      data: this.regParams
    })
    this.regRequest.then(res => {
      const { data, msg, code } = res
      this.setState({
        loading: false
      })
      if(code === 200){
        this.toast.show('申请成功, 即将跳往付款页面', 1500, () => { 
          this.props.history.push(`/invite2/master_phone=${this.state.master_phone}`);
          // window.location.href =  config.shareHost + '/Pay' + `?master_phone=${this.state.master_phone}`;
        })
      }else{
        let showMsg = msg
        if(!showMsg||showMsg.replace(/\s/g,'').length === 0){
          showMsg = '系统异常，跳转付款失败';
        }
        this.toast.show(showMsg, 3500);//window.location.href.split('/invite')[0]
      }
    }).catch(err => {
      this.setState({
        loading: false
      })
      this.toast.show("加入推客失败"+err.message, 3500)
    })
  }

  handleUrl = () => {
    let params = this.props.match.params;
    if(null === params&&!params['phone']){
      this.toast.show('参数错误');
    }
    let master_phone = params.phone;
    this.setState({
      master_phone
    })
    
  }
  componentWillUnmount(){
    
  }

  componentDidMount() {
    this.clipboard = new Clipboard(".copyBtn");
    this.handleUrl()
  }

  render() {
    const { sure, handle } = this.props
    const { show, master_phone, loading, timeout, click, phone } = this.state
    return (
      <div className="regWrap" onClick={handle}>
        <Toast
          ref={(e) => { e && (this.toast = e) }}
          position={'center'}
          show={show}
        />
        {loading ? (
          <div className="Loading">
            <Loading />
          </div>
        ) : null}
        <h2 className="text-right">加入推客</h2>
        <p className="text-right">第一步（1/2）</p>
        <form className="reg">
          <div className='wrap'>
            <span>推荐人</span>
            <input 
                type="text"   
                placeholder="推荐人" 
                className="input" 
                id ='code' 
                required 
                maxLength={11} 
                value={master_phone}
                onChange={(text) => {
                  this.copy()
                }}
                readOnly={true}
            />
            {/* <span className="needBorder copyBtn" data-clipboard-target="#code" onClick={() => {
                this.copy()
            }}>复制</span> */}
          </div>
          <div className='wrap'>
            <span>注册手机号</span>
            <input type="text" placeholder="为了您邀请码安全，请使用输入的手机号注册" className="input" required maxLength={11} onChange={(text) => {
              this.setState({
                phone: text.target.value
              })
            }}/>
          </div>
          <div className='wrap' >
            <span>申请类型</span>
            <select ref={(e) => { e && (this.select = e) }} 
              onFocus = {() => { this.setState({ tri: true }) }} 
              onBlur = {() => { this.setState({ tri: false }); this.setState({ types: this.select.value }) }} >
              <option value ="-1">终身码</option>
              <option value ="30">月码</option>
              <option value="1">半货半款</option>
            </select>
            <img className="tri" src={ this.state.tri ? tri : dtri} alt='搜索' />
          </div>
          <input type="submit" value="下 一 步" className="button interactive" onClick={(e) => {
             e.preventDefault()
             this.reg()
            }} />
        </form>
        {/* <p className='goLogin'>
          <Link to={{ pathname: "My" }}>
            登录
          </Link>
        </p> */}
    </div>
    );
  }
}

export default Reg;



