/**
 * 注册
 */
import React, { Component } from 'react';
import {
    Link
  } from 'react-router-dom';
import '../styles/ShareReg.css';
import Toast from '../components/Toast';
import Loading from '../components/Loading';
// import Share from '../screens/Share';
import Clipboard from 'clipboard';
import request from '../data/request';
import storage from '../data/storage';
import config from '../data/config';
import Tao from '../images/ic_launcher.png';
import tri from '../images/tri.png';
import dtri from '../images/dtri.png';

class Reg extends Component {
  constructor(props) {
    super(props);
    this.state = {
      master_phone: 0,
      phone: '',
      types: -1,
      show: false,
      code: '',
      loading: false,
      timeout: 60,
      click: false,
      tri: false,
      pay: '',//推荐人的创始人账号
    };
  }

  copy = () => {  
    this.clipboard.on('success', (e) => {
      this.toast.show('成功复制', 2000)
      this.setState({
        clipboardFail: 'success'
      });
    });
    this.clipboard.on('error', (e) => {
        this.setState({
            clipboardFail: 'fail'
        });
    });
  }

  regParams = {}//注册的参数

  reg = () => {
    this.regParams = {
      phone: this.state.phone,
      master_phone: this.state.master_phone,
      types: this.state.types
    }
    const { phone } = this.state
    if(!(this.state.phone)){
      this.toast.show("手机号不能为空！")
      return
    }
    this.setState({
      loading: true
    })
    this.regRequest = request.post({
      url: '/api/invite/acceptInvite',
      data: this.regParams
    })
    this.regRequest.then(res => {
      const { data, msg, code } = res
      this.setState({
        loading: false
      })
      if(code == 200){
        this.toast.show('申请成功', 1500, () => { 
          window.location.href = config.shareHost;
        })
      }else{
        this.toast.show(msg, 3500, () => { 
          window.location.href = config.shareHost;
        });
      }
    }).catch(err => {
      this.setState({
        loading: false
      })
      this.toast.show(err.message, 3500)
    })
  }

  handleUrl = () => {
    let master_phone = window.location.href.split("master_phone=")[1];
    this.setState({
      master_phone
    })

    this.zhifubaoRequest = request.get({
      url: '/api/getPayInfo',
      params: {
        phone: master_phone
      }
    })
    this.zhifubaoRequest.then(res => {
      const { data } = res
      this.setState({
        pay: data.pay
      })
    }).catch(err => {
      this.toast.show(err.message, 3500)
    })
  }
  componentWillUnmount(){
    
  }

  componentDidMount() {
    this.clipboard = new Clipboard(".copyBtn");
    this.handleUrl()
  }

  render() {
    const { sure, handle } = this.props
    const { show, master_phone, loading, timeout, click, phone, pay } = this.state
    return (
      <div className="regWrap payWrap" onClick={handle}>
        <Toast
          ref={(e) => { e && (this.toast = e) }}
          position={'center'}
          show={show}
        />
        {loading ? (
          <div className="LoadingWrap">
            <Loading />
          </div>
        ) : null}
        <h2 className="text-right">加入推客</h2>
        <p className="text-right">第二步（2/2）</p>
        <p className='payWay'>付款方式：支付宝</p>
        <div className='payTip'>
          <div className='count'>
            付款账号：
            <input 
              type="text"   
              placeholder="推荐人" 
              className="input" 
              id ='code' 
              required 
              maxLength={11} 
              value={pay}
              onChange={(text) => {
                this.copy()
              }}
              readOnly={true}
            />
            <span className="needBorder copyBtn" data-clipboard-target="#code" onClick={() => {
                this.copy()
            }}>复制</span>
          </div>
          <p className="text-right">支付备注要求：</p>
          <p className="text-right">手机号+推客类型（如月码，终身码）</p>
        </div>
    </div>
    );
  }
}

export default Reg;



